body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

html {
  font-size: 62.5%; }

body {
  font-size: 24px;
  color: #fff;
  background: #C33764;
  /* fallback for old browsers */
  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to bottom, #1D2671, #C33764);
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  background-repeat: no-repeat;
  background-attachment: fixed;
  margin: 0;
  padding: 0;
  height: 100%; }

.App {
  position: relative;
  display: grid;
  grid-template-rows: auto 75px 350px 100px;
  grid-template-areas: "header" "checkbox" "tracker" "footer";
  -webkit-animation: show-content 1s ease 0s;
          animation: show-content 1s ease 0s; }

.header {
  grid-area: header;
  align-self: center; }

.checkbox {
  grid-area: checkbox;
  align-self: end;
  justify-self: center; }

.tracker {
  grid-area: tracker;
  align-self: center; }

.footer {
  grid-area: footer;
  justify-self: center; }

@-webkit-keyframes show-content {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@keyframes show-content {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

.iss {
  display: none;
  position: absolute;
  left: 40%;
  top: 80%;
  -webkit-transform: translate(-50%, 0);
          transform: translate(-50%, 0);
  z-index: 1;
  animation: fly 4s ease-in-out 0s infinite alternate-reverse; }
  .iss img {
    width: 300px;
    height: 150px; }

@-webkit-keyframes fly {
  0% {
    left: 37%;
    -webkit-transform: rotate(-10deg);
            transform: rotate(-10deg); }
  100% {
    left: 40%;
    -webkit-transform: rotate(5deg);
            transform: rotate(5deg); } }

@keyframes fly {
  0% {
    left: 37%;
    -webkit-transform: rotate(-10deg);
            transform: rotate(-10deg); }
  100% {
    left: 40%;
    -webkit-transform: rotate(5deg);
            transform: rotate(5deg); } }

@media (min-width: 960px) {
  .iss {
    display: block; } }

.header {
  border-bottom: 2px solid #fff;
  background: linear-gradient(to top, #222, #333); }
  .header .title {
    color: #fff;
    font-weight: 700;
    margin: 0;
    padding: 0;
    font-family: 'Bungee Outline', cursive;
    font-size: calc(0.03125 * 100vw + 3.5rem);
    text-align: center; }

@media (min-width: 960px) {
  .header .title {
    margin: -30px 0; } }

.tracker {
  text-align: center; }
  .tracker p {
    font-style: italic;
    font-weight: 300;
    font-size: 2.7rem;
    line-height: 3rem; }
    .tracker p span {
      font-weight: 600; }

input[type="checkbox"] {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  width: 40px;
  height: 16px;
  border: 1px solid #fff;
  border-radius: 2px;
  background: linear-gradient(to top, #222, #333);
  position: relative;
  display: inline-block;
  overflow: hidden;
  vertical-align: middle;
  transition: background 0.3s;
  box-sizing: border-box;
  outline: none;
  -webkit-transform: scale(2.8);
          transform: scale(2.8); }

input[type="checkbox"]:after {
  content: '';
  position: absolute;
  top: -1px;
  left: -1px;
  width: 14px;
  height: 14px;
  background: #fff;
  border: 1px solid #aaa;
  border-radius: 2px;
  transition: left 0.1s cubic-bezier(0.785, 0.135, 0.15, 0.86);
  outline: none; }

input[type="checkbox"]:checked {
  background: linear-gradient(to top, #00bd2f, #22df4f); }

input[type="checkbox"]:checked:after {
  left: 23px; }

input[type="checkbox"]:hover:not(:checked):not(:disabled):after,
input[type="checkbox"]:focus:not(:checked):not(:disabled):after {
  left: 0px; }

input[type="checkbox"]:hover:checked:not(:disabled):after,
input[type="checkbox"]:focus:checked:not(:disabled):after {
  left: 22px; }

@media (min-width: 320px) {
  p {
    font-size: calc(2.7rem + 3.125vw - 7px); } }

@media (min-width: 960px) {
  p {
    font-size: calc(2.7rem + 7px); } }

.footer {
  position: fixed;
  bottom: -650px;
  width: 100%; }
  .footer img {
    width: 800px;
    height: 800px;
    overflow: hidden;
    display: table;
    position: relative;
    margin: auto;
    -webkit-animation: rotation 180s linear 0s infinite alternate;
            animation: rotation 180s linear 0s infinite alternate; }

@-webkit-keyframes rotation {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  100% {
    -webkit-transform: rotate(359deg);
            transform: rotate(359deg); } }

@keyframes rotation {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  100% {
    -webkit-transform: rotate(359deg);
            transform: rotate(359deg); } }

